.pjt-block {
    height: 100vh;
    padding: 6rem 3rem 2rem;
    background: url(https://i.pinimg.com/736x/e0/f4/80/e0f480f3cfdae579699f62a70c57d891.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: row;
    position: relative;
}

/* .pjt-boxes {
    display: flex;
    flex-direction: column;
} */

.pjtleft {
    flex: 1;
    align-items: center;
}
.pjtright {
    flex: 1;
    align-items: center;
}
.pjt-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    background-color: rgba(206, 206, 158, 0.745);
    padding: 2rem;
    width: 95%;
    height: 100%;
    border-radius: 10px;
    border-radius: 0px 2px 10px black;
    cursor: pointer;
    overflow: auto;
}

.pjt-block img {
    width: 10rem;
    height: 10rem;
    filter: grayscale(150%) contrast(50%) brightness(50%);
    transition: 100ms ease-in-out;
}
.pjt-box:hover img{
    filter: brightness(110%) contrast(150%);
}
.pjt-box:active img {
    filter: brightness(110%) contrast(150%);
}
.pjt-title {
    color: rgb(205, 108, 10);
    font-size: 30px;
    font-weight: 800;
}
.pjt-content {
    color: rgb(44, 73, 0);
    font-size: 15px;
    font-weight: 700;
}
ul.pjt-list {
    margin-left: 10px;
}
ul.pjt-list li {
    margin-left: 10px;
    list-style: disc;
}
ul.pjt-list li a {
    font-size: 14px;
    color: orangered;
}
.pjt-table {
    width: 100%;
    border-collapse: collapse;
    text-align: left;
}
.pjt-table thead {
    background-color: rgba(128, 128, 128, 0.292);
}
.pjt-table td {
    border: 1px solid #0000005d;
    padding: 5px;
}
.sm-media {
    display: none;
}

/* FOR SMALLER MEDIA */
@media screen and (max-width: 480px) {
    .pjt-block {
        flex-direction: column;
        padding: 3rem 10px 1rem;
    }
    .pjtleft {
        height: 75%;
        height: 300px;
    }
    .pjtright {
        height: 75%;
        height: 300px;
    }
    .pjt-box {
        height: 90%;
        padding: 15px;
        margin-left: 0.5rem;
    }
    .pjt-block img {
        width: 4rem;
        height: 4rem;
    }
    .pjt-title {
        color: rgb(205, 108, 10);
        font-size: 20px;
    }
    .pjt-content {
        font-size: 14px;
    }
    ul.pjt-list li a {
        font-size: 12px;
    }
    .sm-media {
        display: block;
        font-size: 10px;
        font-weight: 700;
        background-color: rgba(0, 0, 255, 0.168);
    }
}