.intro-section {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    height: 100vh;
    background: url(https://st3.depositphotos.com/30440304/32776/i/600/depositphotos_327765946-stock-photo-brushed-painted-abstract-background-brush.jpg);
    background-size: cover;
    background-repeat: no-repeat;
}

.left {
    flex: 1;
}

.right {
    flex: 1;
}

/* LEFT BOX */
.left-wrapper {
    padding: 10%;
    display: flex;
    /* justify-content: space-between; */
    flex-direction: column;
    height: 550px;
}

.left-intro {
    font-size: 40px;
    font-weight: 500;
}

.left-name {
    font-size: 60px;
    font-weight: 700;
}

.left-title {
    height: 50px;
    overflow: hidden;
}

.left-title-item {
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: rgb(136, 188, 58);
}

.left-title-wrapper {
    height: 100%;
    animation: move 2s ease-in-out infinite alternate;
}
@keyframes move {
    50%{
        transform: translateY(-50px);
    }
    100%{
        transform: translateY(-100px);
    }
}

.intro-btn {
    width: max-content;
    padding: 15px;
    border: none;
    background-color: rgb(209, 237, 242);
    cursor: pointer;
    font-size: 15px;
    font-weight: 700;
    margin-top: 10px;
    border-radius: 40px;
    border: 3px solid orangered;
    transition: 500ms;
}
.intro-btn:hover {
    background-color: #000000;
    color: white;
}

/* RIGHT BOX */
.right-wrapper {
    position: relative;
    width: 100%;
    height: 745px;
    overflow: hidden;
}

.right-img {
    display: block;
    width: 100%;
    object-fit: contain;
    position: absolute;
}
.clip-polygon {
    position: absolute;
    clip-path: polygon(100% 0%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
    background: linear-gradient(to bottom left, #121FCF 0%, #CF1512 100%);
    width: 100%;
    height: 100%;
}


/* FOR SMALLER MEDIA */
@media screen and (max-width: 480px) {
    .intro-section {
        flex-direction: column;
    }

    .left-wrapper {
        padding: 1rem;
        justify-content: center;
        height: 400px;
    }
    .left-intro {
        font-size: 20px;
    }
    .left-name {
        font-size: 30px;
    }
    
    .left-title {
        height: 20px;
    }
    .left-title-item {
        height: 25px;
        font-size: 15px;
    }
    .left-title-desc {
        font-size: 12px;
    }
    /* FOR ANIMATON  */
    .left-title-wrapper {
        height: 50%;
        animation: move 3s ease-in-out infinite alternate;
    }
    @keyframes move {
        50%{
            transform: translateY(-25px);
        }
        100%{
            transform: translateY(-50px);
        }
    }
    .right {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .right-wrapper {
        position: absolute;
        height: 370px;
    }
    .intro-btn {
        padding: 6px;
        font-size: 10px;
    }
    .right-img {
        width: 70%;
        height: 112%;
        position: absolute;
        bottom: 0;
    }
}






