.ed-block {
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: space-between;
    position: relative;
    background: url(https://st3.depositphotos.com/30440304/32776/i/600/depositphotos_327765946-stock-photo-brushed-painted-abstract-background-brush.jpg);
    background-size: cover;
    background-repeat: no-repeat;
}

.block-1 {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
    justify-content: space-around;
    flex: 1;
    padding: 7rem 4rem 0;
    position: inherit;
}

.block-2 {
    flex: 2;
    display: grid;
    grid-template-rows: 1fr;
    justify-content: space-around;
    padding: 4rem;
    margin: 0 0.5rem;
    position: inherit;
}

.box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    background:  rgba(237, 234, 230, 0.823);
    border-radius: 2%;
    height: 13rem;
    padding: 2rem;
    overflow: hidden;
    cursor: pointer;
    box-shadow: 0 10px 15px black;
}
.box-wrapper {
    height: 100%;
    transition: 500ms ease-in-out;
}
.box-wrapper:hover {
    transform: translateY(-125%);
    bottom: 0;
}

.row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid black;
    background-color: rgba(237, 234, 230, 0.823);
    height: 20rem;
    box-shadow: 0 10px 15px black;
    overflow: scroll;
}
.block-wrapper {
    display: flex;
    flex-direction: row;
}
.experience-img {
    object-fit: contain;
    width: 50px;
    height: 100px;
    margin: 0 3rem;
    animation: bounce 900ms ease-in-out infinite normal;
}
/* .experience-img:hover {
    animation: bounce 900ms ease-in-out infinite normal;
} */
@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        animation-timing-function: ease-in-out;
        transform: translateY(0);
    }
    40% {
        transform: translateY(-30px);
    }
    60% {
        transform: translateY(-15%);
    }
}
.block {
    padding: 0 1rem;
}
.row > span {
    font-size: 0;
}
.row-title {
    font-size: 18px;
    font-weight: 700;
    padding: 1rem 0;
    transition: 100ms ease-in-out;
}
.row-title i {
    font-size: 11px;
    padding: 2rem;
}
.row-content {
    font-size: 12px;
    font-weight: 500;
    transition: 100ms ease-in-out;
}
ul.row-list li {
    list-style: disc;
}
.row-content:hover {
    font-size: 14px;
}

.school {
    padding: 4rem;
    font-size: 30px;
    font-weight: 700;
    display: flex;
    align-items: center;
    justify-content: center;
    color: black;
}
.school img {
    width: 50px;
    height: 50px; 
    margin: 0 5px;
}
.content {
    color: green;
    font-size: 17px;
    font-weight: 700;
    height: 7rem;
}
.year {
    margin-left: 12rem;
    font-size: 15px;
    font-weight: 700;
    height: 2rem;
}

.polygon {
    position: absolute;
    clip-path: polygon(75% 0%, 100% 50%, 75% 100%, 0% 100%, 25% 50%, 0% 0%);
    background: linear-gradient(to bottom left, #121FCF 0%, #CF1512 100%);
    width: 100%;
    height: 100%;
}


/* FOR SMALLER MEDIA */
@media screen and (max-width: 480px) {

    .block-1 {
        padding: 3rem 1rem 0;
        grid-template-columns: repeat(1, 1fr);
        gap: 0.7rem;
    }
    .school {
        padding: 0 0;
        font-size: 20px;
    }
    .school img {
        width: 25px;
        height: 25px;
        margin: 0 5px;
    }
    .content {
        font-size: 15px;
        padding: 32px 1px 2rem 1rem;
    }
    .content > span {
        font-size: 14px;
    }
    .year {
        font-size: 12px;
        margin-left: 8rem;
    }
    .box {
        height: 3rem;
        padding: 0.5rem;
        transition: 500ms ease-in-out;
    }
    .box:hover {
        height: 15rem;
    }
    .box-wrapper:hover {
        transform: translateY(0);
    }
    .block-2 {
        display: grid;
        grid-template-columns: 1fr;
        padding: 0rem 0.5rem;
    }
    .row > span {
        font-size: 10px;
        font-weight: 500;
    }
    .row {
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        margin: 2rem 0 0;
        padding: 1rem;
    }
    .block-wrapper {
        flex-direction: column;
    }
    .row-content {
        font-size: 13px;
        margin-bottom: 8px;
    }
    .row-content:hover {
        font-size: 13px;
    }
    .experience-img {
        width: 50px;
        height: 50px;
        margin: 10px 3rem;
    }
}