.contact {
    display: flex;
    flex-direction: row;
    height: 100vh;
    background: url(https://img.freepik.com/premium-photo/pink-orange-background-with-soft-wave-pattern_81048-12022.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;
}
.cntBox-1 {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}
.cntBox-2 {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    /* background-color: violet; */
    position: inherit;
}
.contact-form {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    margin: 1rem;
    padding: 1rem;
    border-radius: 5px;
    box-shadow: 0 1px 5px black;
}
.contact-form > input {
    width: 25rem;
    height: 5rem;
    margin: 1rem 0;
    padding: 1rem;
    border: none;
    /* border-bottom: 1px solid black; */
    border-radius: 3px 3px 0 0;
    box-shadow: 0.5px 1px 2px black;
    background-color: #9d565399;
    font-size: 17px;
    font-weight: 500;
}
.contact-form > input::placeholder {
    color: black;
}
.contact-form > textarea {
    width: 25rem;
    border: none;
    font-size: 16px;
    background-color: #9d565399; 
    box-shadow: 0.5px 1px 2px black;
}
.contact-form > textarea::placeholder{
    color: black;
}
.cnt-btn {
    padding: 1rem 1.5rem;
    margin-top: 1rem;
    border: none;
    background-color: rgba(0, 0, 0, 0.675);
    color: white;
    border-radius: 5px;
    font-size: 15px;
    font-weight: 700;
    cursor: pointer;
}
.cnt-btn:hover {
    background-color: black;
}
.cntBox-1 > div {
    position: absolute;
    font-size: 118px;
    font-weight: 900;
    letter-spacing: -10px;
    color: rgba(0, 0, 0, 0.675);
}


/* FOR SMALLER MEDIA */
@media screen and (max-width: 480px) {
    .contact {
        flex-direction: column;
    }
    .contact-form {
        padding: 20px;
    }
    .contact-form > input {
        width: 14rem;
        height: 45px;
        margin: 10px 0;
    }
    .cnt-btn {
        padding: 10px 5.5rem;
    }
    .contact-form > textarea {
        width: 14rem;
        height: 5rem;
    }
    .cntBox-1 > div {
        font-size: 50px;
        padding: 0 3rem 0px;
        letter-spacing: -2px;
    }
    .cntBox-1 {
        height: 400px;
    }
}
